.sidebar{
    display:flex;
    flex-direction: column;
    flex: 0.2;
}

.sidebar__header{
    display: flex;
    justify-content: space-between;
    padding: 20px;
    border-right: 1px solid lightgray;


}
.sidebar__header-right{
    display: flex;
    align-items: space-between;
    min-width: 10vw;
}
.sidebar__header-right > .MuiSvgIcon-Root{ 
    margin-right:3vw;
    font-size: 24px !important;


}

.sidebar__search{
    display: flex;
    align-items: center;
    background-color: #f6f6f6;
    height:39px;
    padding: 10px;
}

.sidebar__searchContainer{
    display: flex;
    align-items: center;
    background-color: white;
    width: 100%;
    height: 35px;
    border-radius: 20px;

}
.sidebar__searchContainer > .MuiSvgIcon-root{
    color: gray;
    padding: 10px;
}

.sidebar__searchContainer > input{
    border: none;
    outline-width: 0; 
    /* very important to remove outline obclick */
    margin-left: 10px;
    width: 100%;
}
.sidebar__chats{
    flex:1;
    background-color: white;
    overflow:scroll

}
